<template>
  <div id="home">
    <new-layout>
      <template slot="center">
        <router-view></router-view>
      </template>
      <template slot="bottom">
        <div id="main__header">
          <div class="wrap">
            <div class="video_tit tab">
              <router-link to="/video">视频</router-link>
            </div>

            <div class="announce_tit tab">
              <router-link to="/announce">文章</router-link>
            </div>
            <div class="encyclopedias_tit tab">
              <router-link to="/encyclopedias">百科</router-link>
            </div>
            <div class="assessment_tit tab" style="display: none">
              <router-link to="/assessment">评估</router-link>
            </div>
            <div class="assessment_tit tab">
              <router-link to="/nearsearch">评估</router-link>
            </div>
          </div>
        </div>
      </template>
    </new-layout>
  </div>
</template>

<script>
    export default {
      name:"home",
      data(){
        return{
          showContent: true
        }
      }
    }
</script>

<style scoped lang="scss">

  $bg_color: #6EAFFF;
  $white: #FFF;

  #home{
    #main__header{
      width: 100%;
      background: $bg_color;
      height: 120px;

      .wrap{
        width: 70%;
        height: 100%;
        margin: auto;
        display: flex;

        .tab{
          flex: 1;
          display: flex;
          align-items: center;
          justify-content: center;

          a{
            color: $white;
            font-size: 35px;
            position: relative;

            &:after{
              content: '';
              width: 60px;
              height: 3.5px;
              background: $white;
              position: absolute;
              left: 8px;
              bottom: -8px;
              border-radius: 30px;
              opacity: 0;
              transition: .3s;
            }

            &.router-link-active:after{
              opacity: 1;
            }
          }
        }
      }
    }
  }
</style>



